.Amulet {
  text-align: center;
}

.Amulet-logo {
  height: 100vmin;
}

.Amulet-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Amulet-title {
  color: #ffffff;
  font-size: calc(14px + 2vmin);
}

.Amulet-content {
  color: #ffffff;
  font-size: calc(12px + 2vmin);
  padding-left: calc(20px + 2vmin);
  padding-right: calc(20px + 2vmin);
}

.Amulet-link {
  color: #09d3ac;
}
